import { HtmlGrid } from "./components/html-grid";

if ("serviceWorker" in navigator) {
  navigator.serviceWorker
    .register("js/sw.js", { scope: "./js/" })
    .catch((e) => {
      console.log("A Failed registering service worker", e);
    });
}

document.addEventListener("DOMContentLoaded", () => {
  HtmlGrid.initialize(document, window.localStorage);
});
