export class GameBoard {
  static RED = "red";
  static WHITE = "white";
  static BLUE = "blue";

  dimensions: number;

  private colors: string[];
  private grid: string[][];

  constructor(dimensions = 3) {
    this.colors = [GameBoard.RED, GameBoard.WHITE, GameBoard.BLUE];
    this.initialize(dimensions);
  }

  initialize(dimensions: number) {
    this.dimensions = dimensions;
    this.grid = new Array(this.dimensions);
    for (let r = 0; r < this.dimensions; ++r) {
      this.grid[r] = new Array(this.dimensions).fill(GameBoard.WHITE);
    }
  }

  getGrid(): string[][] {
    return this.cloneGrid(this.grid);
  }

  setGrid(grid: string[][]) {
    this.grid = this.cloneGrid(grid);
    this.dimensions = this.grid[0].length;
  }

  shuffle() {
    for (let r = 0; r < this.grid.length; ++r) {
      for (let c = 0; c < this.grid[r].length; ++c) {
        this.grid[r][c] =
          this.colors[Math.floor(Math.random() * this.colors.length)];
      }
    }
  }

  tap(row: number, column: number) {
    this.tap_one(row, column);
    this.tap_one(row - 1, column); // N
    this.tap_one(row, column - 1); // W
    this.tap_one(row + 1, column); // S
    this.tap_one(row, column + 1); // E
  }

  isSolved() {
    const color = this.grid[0][0];

    for (let r = 0; r < this.grid.length; ++r) {
      for (let c = 0; c < this.grid[r].length; ++c) {
        if (this.grid[r][c] !== color) {
          return false;
        }
      }
    }
    return true;
  }

  private cloneGrid(src: string[][]): string[][] {
    return JSON.parse(JSON.stringify(src));
  }

  private advance_color(color: string): string {
    const originalIndex = Math.max(0, this.colors.indexOf(color));
    const newIndex = (originalIndex + 1) % this.colors.length;
    return this.colors[newIndex];
  }

  private tap_one(row: number, column: number) {
    if (
      row < 0 ||
      row >= this.dimensions ||
      column < 0 ||
      column >= this.dimensions
    ) {
      return;
    }
    this.grid[row][column] = this.advance_color(this.grid[row][column]);
  }
}
