import { delay } from "./delay";
import { showAlert } from "./show-alert";
import { GameBoard } from "../logic/gameboard";

export class HtmlGrid {
  readonly boardView: HTMLElement;
  readonly newGameButton: HTMLButtonElement;
  readonly gridSizeSelect: HTMLSelectElement;
  readonly buttons: Array<HTMLButtonElement>;

  readonly gameBoard: GameBoard;

  private constructor(document: Document, private storage: Storage) {
    this.boardView = document.querySelector("#board");
    this.newGameButton = document.querySelector("#toolbar__newgame");
    this.gridSizeSelect = document.querySelector("#toolbar__gridsize");
    this.buttons = new Array<HTMLButtonElement>();

    let grid = JSON.parse(storage.getItem("grid") ?? null);
    const gridDimension = grid?.length || 3;
    const controlDimension = parseInt(this.gridSizeSelect.value);
    if (gridDimension !== controlDimension) {
      this.gridSizeSelect.value = `${gridDimension}`;
    }

    this.gameBoard = new GameBoard(gridDimension);

    if (grid) {
      this.gameBoard.setGrid(grid);
    } else {
      this.gameBoard.shuffle();
      grid = this.gameBoard.getGrid();
      storage.setItem("grid", JSON.stringify(grid));
    }

    this.setUpButtons();
    this.updateBoard(grid);

    this.gridSizeSelect.addEventListener("change", () => {
      this.resizeGameBoard();
    });

    this.newGameButton?.addEventListener("click", () => {
      Promise.resolve()
        .then(() => {
          this.gameBoard.shuffle();
          const grid = this.gameBoard.getGrid();
          this.updateBoard(grid);
          storage.setItem("grid", JSON.stringify(grid));
        })
        .then(() => showAlert("Better luck this time!"));
    });
  }

  public static initialize(document: Document, storage: Storage): HtmlGrid {
    return new HtmlGrid(document, storage);
  }

  public resizeGameBoard(): void {
    this.gameBoard.initialize(parseInt(this.gridSizeSelect.value) || 5);
    this.gameBoard.shuffle();

    const grid = this.gameBoard.getGrid();
    this.storage.setItem("grid", JSON.stringify(grid));

    this.setUpButtons();
    this.updateBoard(grid);
  }

  private updateBoard(grid: string[][]) {
    for (let i = 0; i < this.buttons.length; ++i) {
      if (i >= this.gameBoard.dimensions ** 2) {
        break;
      }
      const row = Math.floor(i / this.gameBoard.dimensions);
      const col = i % this.gameBoard.dimensions;
      const el = this.buttons[i] as HTMLElement;
      el.style.backgroundColor = grid?.[row]?.[col] ?? "white";
    }
  }

  private setUpButtons() {
    const form = this.boardView as HTMLElement;
    form.style.gridTemplateColumns = " 1fr".repeat(this.gameBoard.dimensions);
    form.style.gridTemplateRows = " 62px".repeat(this.gameBoard.dimensions);

    this.buttons.length = 0;
    while (this.boardView.lastChild) {
      this.boardView.removeChild(form.lastChild);
    }
    for (let i = 0; i < this.gameBoard.dimensions ** 2; ++i) {
      const button = document.createElement("button");
      button.className = "gameboard__button";
      button.innerHTML = "&nbsp;";

      // TODO seems like we could simplify this
      this.buttons.push(button);
      this.boardView.appendChild(button);

      const row = Math.floor(i / this.gameBoard.dimensions);
      const col = i % this.gameBoard.dimensions;

      button.addEventListener("click", (event) => {
        event.preventDefault();

        this.gameBoard.tap(row, col);

        const grid = this.gameBoard.getGrid();

        this.storage.setItem("grid", JSON.stringify(grid));
        this.updateBoard(grid);

        if (this.gameBoard.isSolved()) {
          showAlert("You did a thing!")
            .then(() => {
              this.gameBoard.shuffle();
              const grid = this.gameBoard.getGrid();
              this.storage.setItem("grid", JSON.stringify(grid));
              this.updateBoard(grid);
            })
            .then(() => delay(0))
            .then(() => {
              showAlert("Now do it again").finally();
            });
        }
        return true;
      });
    }
  }
}
