// TODO make this a class so we can init the HTMLElement,
//    instead of assuming "dialog"
// TODO return OK vs cancel/escape key?

import dialogPolyfill from "dialog-polyfill";

export function showAlert(message: string): Promise<void> {
  return new Promise((resolve) => {
    const dialog = document.querySelector("dialog");
    dialogPolyfill.registerDialog(dialog);

    const dialogContent = dialog.querySelector(".dialog__content");
    const dialogButton = dialog.querySelector(".dialog__button");

    dialogContent.innerHTML = message;
    dialog.showModal();
    dialogButton.addEventListener("click", () => {
      resolve();
    });
  });
}
